:root {
  --textbg: #ec4891;
  --bgcolor: #171717;
  --stextbg: #fda4af;
}

* {
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header {
  width: 100%;
  background-color: var(--bgcolor);
  padding: 0.75rem 2rem;
  display: flex;
  justify-content: space-between;
}

.header h1 {
  color: var(--textbg);
}
.navbar {
  display: flex;
  align-items: center;
}
.navbar li a {
  display: block;
  color: var(--textbg);
  margin-left: 10px;
  padding: 0 1rem;
  font-size: 1.25rem;
  font-weight: 600;
}

.wrapcard {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 1rem 2rem;
  width: 100%;
}
.card {
  width: 30%;
  overflow: hidden;
  background-color: var(--bgcolor);
  border-radius: 5px;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}

.card-text {
  color: var(--textbg);
  padding: 0.25rem 0.5rem;
  margin-bottom: 4px;
}

.card-text p {
  color: var(--stextbg);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
